import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {IOrder, IOrderDetail} from "../../models/order.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {OrderService} from "../../services/order.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {OrderUpsertComponent} from "../order-upsert/order-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {selfEndpoints} from "../../models/config.model";

@Component({
  selector: 'app-order-select',
  templateUrl: './order-select.component.html',
  styleUrls: ['./order-select.component.scss']
})
export class OrderSelectComponent extends BaseComponent {
  @Input() enableAdd: boolean = true

  @Input() searchEndpoint: string = selfEndpoints.endpoint

  items: IOrder[] = []
  @Input() selectedItem: IOrder | null = null
  @Output() selected: EventEmitter<IOrder | null> = new EventEmitter<IOrder | null>()

  protected override componentName(): string {
    return "OrderSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private orderService: OrderService,
              private popupService: DialogService<IOrderDetail>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = selfEndpoints.adminEndpoint
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (event == null) {
      this.selected.emit(null)
    } else if (this.items.find(c => c.order_number == event.order_number)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(
        OrderUpsertComponent, {id: null, dialogUpsert: true, title: event.title}
      )
      popup.componentInstance.onCompleted.subscribe((order: IOrder | null) => {
        this.selected.emit(order)
      })
      popup.componentInstance.onCanceled.subscribe(() => {
        popup.close()
      })
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.ORDERS.SEARCH(params))
    this.orderService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
