import {Component} from '@angular/core';
import {defaultOrderConfig, OrderConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {
  BasicFilterData,
  defaultFilterData,
  parseFilterData,
  toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {OrderService} from "../../services/order.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {ComponentType} from "@angular/cdk/overlay";
import {OrderUpsertComponent} from "../order-upsert/order-upsert.component";
import {IOrder, IOrderDetail, OrderStatus} from "../../models/order.model";
import {ICustomer} from "../../../customers/models/customer.model";
import {HttpErrorResponse} from "@angular/common/http";
import {BaseListComponentV4} from "../../../../../../../base/components/base-list-v4.component";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {AddPaymentComponent} from "../add-payment/add-payment.component";
import {SalesEndpoints} from "../../../sales.endpoints";


@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.scss']
})
export class OrderListComponent extends BaseListComponentV4<IOrder, IOrderDetail> {

  config: OrderConfig = defaultOrderConfig
  filterData: BasicFilterData = defaultFilterData

  orders: IPage<IOrder> = defaultPage

  protected componentName(): string {
    return "OrderListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService,
              dialog: DialogService<IOrderDetail>,
              private activatedRoute: ActivatedRoute,
              private orderService: OrderService,
              private notifier: NotifierService,
              private paymentDialog: DialogService<void>
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.endpoint = selfEndpoints.adminEndpoint
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      this.fetchOrders(this.authService.isAdmin(),{
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: this.translations["Orders"],
        link: null,
        active: true
      }
    ]
  }

  private fetchOrders(asAdmin: boolean, params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.ORDERS.SEARCH(params))
    let url = asAdmin ? SalesEndpoints.ORDERS.SEARCH_FOR_ADMIN(this.config.endpoint, params)
      : SalesEndpoints.ORDERS.SEARCH_FOR_VENDOR(this.config.endpoint, params)
    this.orderService.search(key, url, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.orders = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Order is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfigV2 {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV3<IOrderDetail>> | null {
    return OrderUpsertComponent;
  }


  buildItemsStr(item: IOrder) {
    return item.order_items.map(item => item.product_name).join(', ')
  }

  customerInfo(customer: ICustomer | null): string {
    if (customer == null) return 'Walk-in-customer'
    else if (customer.name != null) return customer.name + '<br/>(' + customer.phone + ')'
    else return customer.phone
  }

  onCancelClick(orderId: number) {
    if (!confirm("Are you sure you want to cancel this order?")) {
      return
    }
    this.changeStatus(orderId, OrderStatus.DELIVERY_FAILED)
  }

  private changeStatus(orderId: number, status: OrderStatus) {
    this.loading = true
    let key = this.componentKey(EventKey.ORDERS.CHANGE_ORDER_STATUS(orderId, status))
    this.orderService.changeStatus(key, orderId, status, null)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false
      this.notifier.success("Success", "Order has been canceled.")
      this.ngOnInit()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  isCanceled(item: IOrder) {
    return item.status === OrderStatus.DELIVERY_FAILED;
  }

  isConfirmed(item: IOrder) {
    return item.status === OrderStatus.CONFIRMED;
  }


  onAddPaymentClick(order: IOrder) {
    let dialogRef = this.paymentDialog.openWithConfig(AddPaymentComponent, {
      width: 'auto',
      height: 'auto',
      data: {
        id: order.id,
        dueAmount: order.due_amount,
        paymentAmount: order.due_amount,
        maxAmount: order.due_amount
      }
    })
    let component = dialogRef.componentInstance
    component.onCanceled.subscribe(() => {
      dialogRef.close()
    })
    component.onCompleted.subscribe(() => {
      dialogRef.close()
      this.ngOnInit()
    })
  }

  onFinishSaleClick(item: IOrder) {
    this.changeStatus(item.id, OrderStatus.CONFIRMED)
  }
}
