import {Component, ElementRef, Inject, ViewChild} from "@angular/core";
import {defaultOrderConfig, OrderConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {OrderService} from "../../services/order.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {IOrder, IOrderDetail, IOrderReq, OrderStatus} from "../../models/order.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {IStock} from "../../../../inventory/stocks/models/stock.model";
import {ICustomer, ICustomerReqForOrder} from "../../../customers/models/customer.model";
import {ValidationUtil} from "../../../../../../../utils/validation.util";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {StockSelectComponent} from "../../../../inventory/stocks/components/stock-select/stock-select.component";
import {StockService} from "../../../../inventory/stocks/services/stock.service";
import {InventoryEndpoints} from "../../../../inventory/inventory.endpoints";


@Component({
  selector: 'app-order-upsert',
  templateUrl: './order-upsert.component.html',
  styleUrls: ['./order-upsert.component.scss']
})
export class OrderUpsertComponent extends BaseUpsertComponentV3<IOrderDetail> {
  @ViewChild("stockSelect") stockSelect: StockSelectComponent
  @ViewChild('skuInput') skuInput!: ElementRef;

  config: OrderConfig = defaultOrderConfig

  orderForm = new FormGroup({
    local_customer: new FormGroup({
      name: new FormControl<string | null>(null, []),
      phone: new FormControl<string | null>(null, []),
      email: new FormControl<string | null>(null, []),
    }),
    // cart_items: new FormArray([], Validators.required),
    date: new FormControl<Date>(new Date(), [Validators.required]),
    coupon_code: new FormControl<string | null>(null, []),
    note: new FormControl<string | null>(null, []),
    // extra
    customer_id: new FormControl<number | null>(null, []),
    delivery_address_id: new FormControl<number | null>(null, []),
    delivery_option_id: new FormControl<number | null>(null, []),
    distance: new FormControl<number | null>(null, []),
    is_local: new FormControl<boolean | null>(null, []),
    payment_method: new FormControl<string | null>(null, []),
    pickup_schedule_id: new FormControl<number | null>(null, []),
  })

  selectedCustomer: ICustomer | null = null
  selectedStock: IStock | null = null

  selectedPaymentStatus: string = "PAID"
  paymentAmount: number = 0
  showPaymentAmount: boolean = false

  cart: {
    stock: IStock,
    quantity: number,
    availableQuantity: number,
    discount: number,
  }[] = []

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private orderService: OrderService,
              private notifier: NotifierService,
              private stockService: StockService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.title != null) {
      // this.orderForm.controls.title.setValue(dialogData.title)
    }
  }

  override ngOnInit() {
    super.ngOnInit();
  }

  protected override componentName(): string {
    return "OrderUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Orders",
        link: "/admin/orders",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.ORDERS.GET(exId))
    this.orderService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.orderForm.valid) {
      return
    }
    let req = this.orderForm.value as IOrderReq
    req.cart_items = this.cart.map(item => {
      return {
        stock_id: item.stock.id,
        quantity: item.quantity,
        discount: item.discount
      }
    })
    req.delivery_option_id = 1
    req.payment_method = "STORE_PAYMENT"
    req.is_local = true
    let customer = this.orderForm.controls.local_customer.value as ICustomerReqForOrder
    // if phone isn't entered, customer is not needed
    if (customer.phone == null) {
      req.local_customer = null
    } else {
      if (!ValidationUtil.isValidPhone(customer.phone)) {
        this.notifier.warning("Invalid phone number", "Please enter a valid phone number")
        return;
      }
      req.local_customer = customer
    }

    // If order isn't fully paid, then must select a customer
    if (this.selectedPaymentStatus !== 'PAID' && req.local_customer == null) {
      this.notifier.warning(
        "Customer is required",
        "Unpaid order must have a customer. This will help you track the payment and get paid later on.",
        () => {
        }, 10000)
      return;
    }

    this.upsert(this.exId, req)
  }


  onStockSelected(stock: IStock | null) {
    if (stock == null) {
      return
    }
    let item = {
      stock: stock,
      quantity: 1,
      availableQuantity: stock.quantity,
      discount: stock.discount_percentage
    }
    let existingIndex = this.cart.findIndex(c => c.stock.id == stock.id)
    console.log(existingIndex)
    if (existingIndex >= 0) {
      this.cart[existingIndex].quantity += 1
    } else {
      this.cart.push(item)
    }
    this.stockSelect.clear()
  }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null, req: IOrderReq) {
    this.loading = true
    let key = this.componentKey(EventKey.ORDERS.UPSERT(exId))
    this.orderService.upsert(key, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Order has been " + (exId ? "updated" : "created"))
      this.orderForm.reset();
      let orders = meta.data as IOrder[]
      orders.forEach(order => {
        this.resolvePayment(order)
      })
      this.cleanUpPageAndPrepareForNextOrder()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  makePayment(id: number, amount: number, paymentMethod: string, onSuccess: () => void = () => {
  }) {
    this.loading = true
    let key = this.componentKey(EventKey.ORDERS.MAKE_PAYMENT(id))
    this.orderService.makePayment(key, id, amount, paymentMethod)
    this.subscribe(key, null, (value, meta) => {
      // this.notifier.success("Success", "Payment has been updated")
      this.loading = false
      onSuccess()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IOrder) {
    // this.orderForm.controls.title.setValue(n.title)
    // this.orderForm.controls.description.setValue(n.description)
    // this.orderForm.controls.image.setValue(n.image == null ? null : n.image)
  }

  onCustomerSelected(customer: ICustomer | null) {
    this.selectedCustomer = customer
    if (customer == null) {
      return
    }
    this.orderForm.controls.local_customer.controls.name.setValue(customer.name)
    this.orderForm.controls.local_customer.controls.phone.setValue(customer.phone)
    this.orderForm.controls.local_customer.controls.email.setValue(customer.email)
  }

  onDeleteItem(index: number) {
    this.cart.splice(index, 1)
  }

  onQtyChange(index: number, qty: number) {
    let item = this.cart[index]
    item.quantity = qty

    if (item.quantity < 1) {
      this.cart.splice(index, 1)
    }
  }

  onQtySetEvent(i: number, event: Event) {
    let qty = (event.target as HTMLInputElement).value
    this.onQtyChange(i, parseInt(qty))
  }

  onDiscountChange(index: number, discount: number) {
    if (discount < 0) discount = 0
    if (discount > 100) discount = 100

    this.cart[index].discount = discount
  }

  onDiscountSetEvent(i: number, event: Event) {
    let discountStr = (event.target as HTMLInputElement).value
    if (discountStr == "") discountStr = "0"

    let discount = parseInt(discountStr)
    if (isNaN(discount)) discount = 0
    this.onDiscountChange(i, discount)
  }

  calculateSubtotal(item: { stock: IStock; quantity: number; discount: number }, withDiscount: boolean = true) {
    let price = item.stock.retail_price * item.quantity
    if (!withDiscount) {
      return price
    }
    let discount = price * item.discount / 100
    return price - discount
  }

  calculateTotalDiscount() {
    return this.cart.reduce((a, b) => a + this.calculateDiscount(b), 0)
  }

  totalUnitPrices() {
    return this.cart.reduce((a, b) => a + (b.stock.avg_unit_price * b.quantity), 0)
  }

  calculateDiscount(item: { stock: IStock; quantity: number; discount: number }) {
    return item.stock.retail_price * item.quantity * item.discount / 100
  }

  calculateTotal() {
    return this.cart.reduce((a, b) => a + this.calculateSubtotal(b, false), 0)
  }

  calculateGrandTotal() {
    return this.cart.reduce((a, b) => a + this.calculateSubtotal(b, true), 0)
  }

  calculateTotalTax() {
    return 0
  }

  onPaymentStatusSelected(event: Event) {
    this.selectedPaymentStatus = (event.target as HTMLInputElement).value
    switch (this.selectedPaymentStatus) {
      case "PAID":
        this.paymentAmount = this.calculateGrandTotal()
        break
      case "PARTIALLY_PAID":
        this.paymentAmount = Math.round(this.calculateGrandTotal() / 2)
        break
      case "UNPAID":
        this.paymentAmount = 0
        break
    }
    this.showPaymentAmount = this.selectedPaymentStatus === 'PARTIALLY_PAID'
    console.log(typeof this.selectedPaymentStatus)
  }

  onPaymentAmountSet(event: Event) {
    let amountStr = (event.target as HTMLInputElement).value
    if (amountStr == "") amountStr = "0"

    let amount = parseInt(amountStr)
    if (amount < 0) amount = 0
    if (amount > this.calculateGrandTotal()) amount = this.calculateGrandTotal()

    this.paymentAmount = amount

    console.log(this.paymentAmount)

  }

  private resolvePayment(order: IOrder) {
    if (this.selectedPaymentStatus === 'PAID') {
      this.makePayment(order.id, this.calculateGrandTotal(), 'STORE_PAYMENT')
    } else if (this.selectedPaymentStatus === 'PARTIALLY_PAID') {
      this.makePayment(order.id, this.paymentAmount, 'STORE_PAYMENT', () => {
        this.confirmOrder(order.id)
      })
    } else if (this.selectedPaymentStatus === 'UNPAID') {
      this.confirmOrder(order.id)
    }
  }

  private confirmOrder(orderId: number) {
    this.loading = true
    let status = OrderStatus.CONFIRMED
    let key = this.componentKey(EventKey.ORDERS.CHANGE_ORDER_STATUS(orderId, status))
    this.orderService.changeStatus(key, orderId, status, null)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false
      // this.notifier.success("Success", "Order has been confirmed")
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private cleanUpPageAndPrepareForNextOrder() {
    this.cart = []
    this.selectedCustomer = null
    this.orderForm.controls.date.setValue(new Date())
    this.selectedPaymentStatus = "PAID"
    this.paymentAmount = 0
  }

  onClear() {
    let confirmed = confirm('Are you sure you want to clear all items?')
    if (confirmed) {
      this.cart = []
    }
  }

  private selectBySku(sku: string) {
    let key = this.componentKey(EventKey.STOCKS.GET_BY_SKU(sku))
    this.stockService.fetchBySku(key, InventoryEndpoints.STOCKS.STOCK_ENDPOINT_V2, sku)
    this.subscribe(key, null, (value, meta) => {
      let stock = meta.data as IStock
      this.onStockSelected(stock)
      this.skuInput.nativeElement.value = '';
    })
  }

  onSkuEntered(event: Event) {
    let sku = (event.target as HTMLInputElement).value
    this.selectBySku(sku)
  }
}
