import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {SalesEndpoints} from "../../sales.endpoints";

export interface CustomerConfig extends BaseConfig {
}

export let defaultCustomerConfig: CustomerConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: SalesEndpoints.CUSTOMERS.CUSTOMER_ENDPOINT,
  delEndpoint: SalesEndpoints.CUSTOMERS.CUSTOMER_ENDPOINT
}

export let selfEndpoints = {
  endpoint: SalesEndpoints.CUSTOMERS.CUSTOMER_ENDPOINT,
  adminEndpoint: SalesEndpoints.CUSTOMERS.CUSTOMER_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/admin/sales/customers",
  create: "/admin/sales/customers/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/sales/customers/" + id + '/update'
  }
}

