import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IOrder, IOrderReq} from "../models/order.model";
import {SalesEndpoints} from "../../sales.endpoints";

@Injectable({
  providedIn: 'root'
})
export class OrderService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, url: string, params: IParams) {
    // let url = SalesEndpoints.ORDERS.SEARCH(endpoint, params)
    this.http.get<IPage<IOrder>>(url)
      .subscribe((order: IPage<IOrder>) => {
        this.eventBusService.publish(key, order)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, url, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<IOrder>(SalesEndpoints.ORDERS.GET(id))
      .subscribe((order: IOrder) => {
        this.eventBusService.publish(key, order)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, orderReq: IOrderReq, exId: number | null) {
    let endpoint = exId == null ? SalesEndpoints.ORDERS.CREATE() : SalesEndpoints.ORDERS.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, orderReq)
      : this.http.patch(endpoint, orderReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, orderReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

  makePayment(key: string, orderId: number, amount: number, paymentMethod: string) {
    let endpoint = SalesEndpoints.ORDERS.MAKE_PAYMENT(orderId, {amount: amount, pay_with: paymentMethod})
    this.http.get<string>(endpoint).subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.makePayment(key, orderId, amount, paymentMethod)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

  changeStatus(key: string, orderId: number, status: string, note: string | null = null) {
    let endpoint = SalesEndpoints.ORDERS.CHANGE_STATUS(orderId, {
      order_id: orderId, status: status, note: note
    })
    this.http.patch<string>(endpoint, {}).subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.changeStatus(key, orderId, status, note)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }


}
