import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {ICustomer, ICustomerDetail, ICustomerReq} from "../models/customer.model";
import {SalesEndpoints} from "../../sales.endpoints";

@Injectable({
  providedIn: 'root'
})
export class CustomerService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = SalesEndpoints.CUSTOMERS.SEARCH(endpoint, params)
    this.http.get<ResponseData<ICustomer>>(url)
      .subscribe((customer: ResponseData<ICustomer>) => {
        this.eventBusService.publish(key, customer)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, endpoint: string, id: number) {
    this.http.get<ResponseData<ICustomerDetail>>(SalesEndpoints.CUSTOMERS.GET(endpoint, id))
      .subscribe((customer: ResponseData<ICustomerDetail>) => {
        this.eventBusService.publish(key, customer.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, endpoint, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, customerReq: ICustomerReq, exId: number | null) {
    let endpoint = exId == null ? SalesEndpoints.CUSTOMERS.CREATE() : SalesEndpoints.CUSTOMERS.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, customerReq)
      : this.http.patch(endpoint, customerReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, customerReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
