import {Component, Inject} from '@angular/core';
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {CustomerConfig, defaultCustomerConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {CustomerService} from "../../services/customer.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {ICustomerDetail, ICustomerReq} from "../../models/customer.model";
import {IVendorBrief} from "../../../../inventory/vendors/models/vendor.model";

@Component({
  selector: 'app-customer-upsert',
  templateUrl: './customer-upsert.component.html',
  styleUrls: ['./customer-upsert.component.scss']
})
export class CustomerUpsertComponent extends BaseUpsertComponentV2<ICustomerDetail> {

  config: CustomerConfig = defaultCustomerConfig

  customerForm = new FormGroup({
    name: new FormControl<string | null>(null,),
    phone: new FormControl('', [
        Validators.required,
        Validators.minLength(11),
        Validators.maxLength(11),
        Validators.pattern("^[0-9]*$")
      ]
    ),
    email: new FormControl<string | null>(null),
    vendor_id: new FormControl<number | null>(null, [Validators.required]),
  })

  selectedVendor: IVendorBrief | null = null

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private customerService: CustomerService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.phone != null) {
      this.customerForm.controls.phone.setValue(dialogData.phone)
    }

  }

  protected override componentName(): string {
    return "CustomerUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Customers",
        link: "/admin/customers",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.CUSTOMERS.GET(exId))
    this.customerService.fetchSingle(key, this.config.searchEndpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.customerForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.CUSTOMERS.UPSERT(exId))
    let req = this.customerForm.value as ICustomerReq
    this.customerService.upsert(key, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Customer has been " + (exId ? "updated" : "created"))
      this.customerForm.reset();
      this.onSuccess(meta.data.body)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ICustomerDetail) {
    this.selectedVendor = n.vendor as unknown as IVendorBrief

    this.customerForm.controls.name.setValue(n.name)
    this.customerForm.controls.phone.setValue(n.phone)
    this.customerForm.controls.email.setValue(n.email)
    this.customerForm.controls.vendor_id.setValue(n.vendor.id)
  }

  onVendorSelected(vendor: IVendorBrief | null) {
    this.selectedVendor = vendor
    this.customerForm.controls.vendor_id.setValue(
      vendor != null ? vendor.id : null
    )
  }
}
