import {Component, EventEmitter, Inject} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {OrderService} from "../../services/order.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseDialogComponent} from "../../../../../../../base/components/base-dialog.component";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";

@Component({
  selector: 'app-add-payment',
  templateUrl: './add-payment.component.html',
  styleUrls: ['./add-payment.component.scss']
})
export class AddPaymentComponent extends BaseComponent implements BaseDialogComponent<void> {

  orderId: number | null = null
  dueAmount: number = 0
  paymentAmount: number = 0
  maxAmount: number = 0

  onCanceled: EventEmitter<void> = new EventEmitter<void>();
  onCompleted: EventEmitter<void | null> = new EventEmitter<void | null>();

  constructor(
    eventBus: EventBusService,
    router: Router,
    subContainer: SubscriptionContainerService,
    private orderService: OrderService,
    private notifier: NotifierService,
    @Inject(MAT_DIALOG_DATA) dialogData: any
  ) {
    super(eventBus, router, subContainer)
    console.log(dialogData)

    if (dialogData.id != null) {
      this.orderId = dialogData.id
    }
    if (dialogData.dueAmount != null) {
      this.dueAmount = dialogData.dueAmount
    }
    if (dialogData.paymentAmount != null) {
      this.paymentAmount = dialogData.paymentAmount
    }
    if (dialogData.maxAmount != null) {
      this.maxAmount = dialogData.maxAmount
    }
  }

  protected componentName(): string {
    return AddPaymentComponent.name;
  }

  ngOnInit(): void {
    this.loading = false
  }


  onCancelClick() {
    this.onCanceled.emit()
  }

  onPaymentClick() {
    if (this.orderId == null || this.paymentAmount < 0 || this.paymentAmount > this.maxAmount) {
      this.notifier.show(
        "warning",
        "Invalid payment amount",
        "Payment amount must be greater than 0 and less than due amount"
      )
      return
    }

    this.makePayment(this.orderId, this.paymentAmount, "STORE_PAYMENT")
  }

  makePayment(id: number, amount: number, paymentMethod: string) {
    this.loading = true
    let key = this.componentKey(EventKey.ORDERS.MAKE_PAYMENT(id))
    this.orderService.makePayment(key, id, amount, paymentMethod)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Payment has been updated")
      this.loading = false
      this.onCompleted.emit()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }
}
