import {BaseEndpoint} from "../../../../base/endpoints/base.endpoint";
import {IParams} from "../../../../base/endpoints/iparams";
import {environment} from "../../../../../environments/environment";

export class SalesEndpoints extends BaseEndpoint {
  static ORDERS = {
    ORDER_ENDPOINT: environment.base_url + '/api/v1/orders',
    ORDER_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/orders',
    SEARCH_FOR_VENDOR(endpoint: string, params: IParams) {
      return endpoint.concat("/vendor-orders").concat(BaseEndpoint.toParamString(params));
    },
    SEARCH_FOR_ADMIN(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.ORDER_ENDPOINT
    },
    GET(id: number): string {
      return this.ORDER_ENDPOINT + '/' + id
    },
    UPDATE(id: number): string {
      return this.ORDER_ENDPOINT + '/' + id
    },
    MAKE_PAYMENT(id: number, params: IParams): string {
      return this.ORDER_ENDPOINT + '/' + id + '/pay' + BaseEndpoint.toParamString(params)
    },
    CHANGE_STATUS(id: number, params: IParams): string {
      return this.ORDER_ENDPOINT + '/' + id + '/change-status' + BaseEndpoint.toParamString(params)
    },
  }

  static CUSTOMERS = {
    CUSTOMER_ENDPOINT: environment.base_url + '/api/v2/customers',
    CUSTOMER_ENDPOINT_ADMIN: environment.base_url + '/api/v2/admin/customers',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(): string {
      return this.CUSTOMER_ENDPOINT
    },
    GET(endpoint: string, id: number): string {
      return endpoint + '/' + id
    },
    UPDATE(id: number): string {
      return this.CUSTOMER_ENDPOINT + '/' + id
    },
  }
}
