import {BaseConfig, BaseConfigV2} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {SalesEndpoints} from "../../sales.endpoints";

export interface OrderConfig extends BaseConfigV2 {
}

export let defaultOrderConfig: OrderConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: false,
  showUserFilter: false,
  showDeleteButton: true,
  endpoint: SalesEndpoints.ORDERS.ORDER_ENDPOINT,
  delEndpoint: SalesEndpoints.ORDERS.ORDER_ENDPOINT
}

export let selfEndpoints = {
  endpoint: SalesEndpoints.ORDERS.ORDER_ENDPOINT,
  adminEndpoint: SalesEndpoints.ORDERS.ORDER_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/admin/sales/orders",
  create: "/admin/sales/orders/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/sales/orders/" + id + '/update'
  }
}

